body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  background-image: linear-gradient(rgb(9, 51, 241),rgb(6, 218, 247))
}
.boardBtn {
    height: 100px;
    width: 100px;
    font-size: 40px;
    border-radius: 10px;
    background-color: orangered;
    color: blue;
    border: 3px solid white;
    margin: 10px;
}
.boardBtn:hover {
    border-color: darkgreen;
    color: darkgreen;
}
.board {
    margin-top: 100px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
}
.clear{
    padding: 10px;
    font-size: large;
    border: 5px dashed green;
    margin-left: auto;
    margin-right: auto;
    border-radius: 10px;
    margin: 5px;
}

.clear:hover{
    border:  2px solid yellow;
    cursor: pointer;
}
.displayMsg{
    font-size: x-large;
    width: 100%;
    text-align: center;
}
.header{
    color: yellow;
    width: 100%;
    text-align: center;
    font-size: xx-large
}
